@import url(https://fonts.googleapis.com/css?family=Khula:700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body {
  background: #111;
}
.container {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.lineClassName {
  font-family: Khula;
  font-size: 4em;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: 850px;
  overflow: hidden;
  display: block;
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

._Wf0GC {
  -webkit-animation: _dHnpo 0.6s infinite ease;
          animation: _dHnpo 0.6s infinite ease;
  display: inline-block;
  position: relative;
  top: -0.14em;
  left: 5px;
  margin-left: 5px;
}

@-webkit-keyframes _dHnpo {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes _dHnpo {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

